<!-- 用户列表 | created by JanvierCheng -->
<template>
  <div id="userListDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav">
              <my-location title="用户列表"></my-location>
              <my-navigat activ="4-1"></my-navigat>
          </div>
          <div class="ho-cotet">
              <div>
                  <el-button-group>
                    <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                    <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                    <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35" >
                    </el-table-column>
                    <el-table-column
                      prop="TB_userName"
                      label="用户名"
                      width="250">
                    </el-table-column>
                    <!-- <el-table-column
                      prop="TB_pass"
                      label="密码"
                      width="250"
                      :show-overflow-tooltip="true">
                    </el-table-column> -->
                    <el-table-column
                      prop="TB_roleName"
                      label="角色">
                    </el-table-column>
                  </el-table>
              </div>
              <el-dialog title="用户信息" v-model="dialogFormVisible">
                  <el-form :model="form" :rules="formRules" ref="form">
                    <el-form-item label="用户名" :label-width="formLabelWidth" prop="userName">
                      <el-input v-model="form.userName" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" :label-width="formLabelWidth" prop="pass">
                      <el-input v-model="form.pass" auto-complete="off" type="password"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码" :label-width="formLabelWidth" prop="passConf">
                      <el-input v-model="form.passConf" auto-complete="off" type="password" :disabled="form.pass==''"></el-input>
                    </el-form-item>
                    <el-form-item label="用户角色" :label-width="formLabelWidth" prop="role">
                      <el-select v-model="form.role" placeholder="请选择角色">
                        <el-option :value="item.roleno" :label="item.rolename" v-for="item in roleSet"></el-option>
                      </el-select>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm()">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>

  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import axios from 'axios';
import config from './../publicAPI/config'

export default {
  // name: 'login',
  components: {
      myHeader,
      myLocation,
      myNavigat
  },
  data(){
      var validatePass = (rule, value, callback) => {
          if (value === '' && this.currentOpert) {
              callback(new Error('请输入密码'));
          } else {
              if (this.form.passConf !== '') {
                this.$refs.form.validateField('passConf');
              }
              callback();
          }
      };
      var validatePass2 = (rule, value, callback) => {
          if (value === '' && this.form.pass !== '' ) {
              callback(new Error('请再次输入密码'));
          } else if ((value === '' || value === undefined) && this.form.pass === '' && !this.currentOpert) {
              callback();
          } else if (value !== this.form.pass ) {
              console.log(value);
              console.warn(this.form.pass);
              callback(new Error('两次输入密码不一致!'));
          } else {
              callback();
          }
      };
      return {
          URL: config.rootURL,
          tableData: [],
          inputShow: false,
          multipleSelection: [],
          dialogFormVisible: false,
          currentOpert: true,  // true新增，false修改
          form: {
              userName: '',
              pass: '',
              passConf: '',
              role: ''
          },
          formRules:{
              userName: [
                  { required: true, message: '请输入用户名', trigger: 'blur' },
              ],
              pass: [
                  { validator: validatePass, trigger: 'blur' },
              ],
              passConf: [
                  { validator: validatePass2, trigger: 'blur' },
              ],
          },
          roleSet: [],
          formLabelWidth: '120px'
      }
  },
  methods:{
      onAdd(){
          this.dialogFormVisible = true;
          this.currentOpert = true;
      },
      onEdit(){
          if(this.multipleSelection.length==1){
              this.dialogFormVisible = true;
              this.currentOpert = false;
              console.log(JSON.stringify(this.multipleSelection[0]));
              this.form.userName = this.multipleSelection[0].TB_userName;
              this.form.role = this.multipleSelection[0].TB_role;
          }
      },
      getList(){
          let that = this
          axios.post(that.URL + '/user/list', {})
            .then((res) => {
                if(res.data.state) {
                  that.tableData = []
                  for(var item of res.data.value.data){
                    that.tableData.push({
                        TB_userName: item.username,
                        TB_role: item.role,
                        TB_roleName: item.roleName
                    })
                  }
                } else {
                  that.$message.error(res.data.value);
                }

            })
            .catch((error) => {
                console.error(error);
            })
      },
      resetForm(){
          this.dialogFormVisible = false
          // this.form.role = [];
          this.form = {
              userName: '',
              pass: '',
              role: ''
          },
          this.$refs['form'].resetFields()
      },
      getSelect(){
          let that = this
          axios.get(that.URL + '/role/selection')
            .then((res) => {
                that.roleSet = [];
                for(var item of res.data.value){
                  that.roleSet.push({
                      rolename: item.rolename,
                      roleno: item.roleno
                  });
                }

            })
            .catch((error) => {
                console.error(error);
            })
      },
      onDelete(){
         let that = this;
         if(this.multipleSelection.length==1){
           that.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
             confirmButtonText: '确定',
             cancelButtonText: '取消',
             type: 'warning'
           }).then(() => {
               let str = "";

               axios.post(that.URL+'/user/deletion', {
                  username: that.multipleSelection[0].TB_userName
               })
               .then(function (res) {
                   if(res.data.state){
                       that.$message({
                         type: 'success',
                         message: res.data.value
                       });
                       that.getList();
                   }else{
                       that.$message({
                         type: 'error',
                         message: res.data.value
                       });
                   }
               })
               .catch(function (error) {
               });

           }).catch((error) => {
               // this.$message.error(error);
           });
         }else{
           this.$message.error('不能删除多个！');
         }

      },
      onConfirm(){

        let that = this
        var param = {
            username: this.form.userName,
            password: this.form.pass,
            role: this.form.role,
        }
        this.$refs['form'].validate((valid) => {
            if (valid) {
                if( this.currentOpert){//新增
                    axios.post(that.URL+'/user/addition', param)
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.dialogFormVisible = false
                            that.getList();
                            that.resetForm();
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                      console.error(error);
                    });
                    this.dialogFormVisible = false
                }else{//修改
                    axios.post(that.URL+'/user/update', param)
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.dialogFormVisible = false
                            that.getList();
                            that.resetForm();
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                      console.error(error);
                    });
                    this.dialogFormVisible = false
                }
            } else {
              console.log('error submit!!');
              return false;
            }
        });
      },
      handleSelectionChange(val) {
          this.multipleSelection = val;
        //   console.log(this.multipleSelection);
      },
  },
  created(){
    this.getList();
    this.getSelect();
  }
}
</script>

<style scoped>
.el-button-group{
    margin-top: 5px;
    margin-bottom: 20px;
}
.el-input,.el-select{
    width: 500px;
}
</style>



