                      </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="角色管理" :label-width="formLabelWidth" prop="f18">
                      <el-checkbox-group v-model="form.f18">
                        <el-checkbox label="1801" name="f18" >查看</el-checkbox>
                        <el-checkbox label="1802" name="f18" >添加</el-checkbox>
                        <el-checkbox label="1803" name="f18" >修改</el-checkbox>
                        <el-checkbox label="1804" name="f18" >删除</el-checkbox>
                      </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="主页设置" :label-width="formLabelWidth" prop="f16">
                      <el-checkbox-group v-model="form.f16">
                        <el-checkbox label="1601" name="f16" >查看</el-checkbox>
                        <el-checkbox label="1602" name="f16" >添加</el-checkbox>
                        <el-checkbox label="1603" name="f16" >修改</el-checkbox>
                        <el-checkbox label="1604" name="f16" >删除</el-checkbox>
                      </el-checkbox-group>
                    </el-form-item>
                    <div class="clearfix"></div>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('form')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>

  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import axios from 'axios';
import config from './../publicAPI/config'

export default {
  // name: 'login',
  components: {
      myHeader,
      myLocation,
      myNavigat
  },
  data(){
      return {
          URL: config.rootURL,
          tableData: [],
          inputShow: false,
          multipleSelection: [],
          currentOpert: true,  // true新增，false修改
          dialogFormVisible: false,
          form: {
            rolename: '',
            roleno: '',
            f01: [],
            f02: [],
            f03: [],
            f04: [],
            f05: [],
            f06: [],
            f07: [],
            f08: [],
            f09: [],
            f10: [],
            f11: [],
            f12: [],
            f13: [],
            f14: [],
            f15: [],
            f16: [],
            f17: [],
            f18: [],
          },
          formRules: {
            rolename: [
              { required: true, message: '请输入角色名称', trigger: 'blur' },
            ],
            roleno: [
              { required: true, message: '请输入角色编号', trigger: 'blur' },
            ]
          },
          formLabelWidth: '180px'
      }
  },
  methods:{
      onAdd(){
          this.dialogFormVisible = true;
          this.currentOpert = true;
      },
      onEdit(){
          if(this.multipleSelection.length==1){
              this.dialogFormVisible = true;
              this.currentOpert = false;
              this.form.rolename = this.multipleSelection[0].TB_rolename;
              this.form.roleno = this.multipleSelection[0].TB_id;
              // console.log(JSON.stringify(this.multipleSelection[0]));

              let that = this;
              // that.form.f18 = ["1801","1802"];
              for(var starfromTB in this.multipleSelection[0]) {
                // console.warn(starfromTB);
                if(starfromTB !== "TB_rolename" &&  starfromTB !== "TB_id" && starfromTB !== "TB_roleno") {
                  let fname = "f" + starfromTB.slice(0,2);
                  // console.log(fname);
                  (that.form)[fname].push(starfromTB);
                }
              }
          }
      },
      onDelete(){

         let that = this;
         that.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
           confirmButtonText: '确定',
           cancelButtonText: '取消',
           type: 'warning'
         }).then(() => {
             let str = "";
             for(var item of this.multipleSelection){
                 str = str + item.TB_roleno + ','
             }
             axios.post(that.URL+'/role/deletion', {
               roleno: str
             })
             .then(function (res) {
                 if(res.data.state){
                     that.$message({
                       type: 'success',
                       message: res.data.value
                     });
                     that.getList();
                 }else{
                     that.$message({
                       type: 'error',
                       message: res.data.value
                     });
                 }
             })
             .catch(function (error) {

             });

         }).catch((error) => {
             // this.$message.error(error);
         });

      },
      getList(){
        let that = this
        axios.post(that.URL + '/role/list', {})
          .then((res) => {
              if(res.data.state) {
                that.tableData = [];
                let index = 0;
                for(var item of res.data.value.data){

                  that.tableData.push({
                      TB_id: item.roleno,
                      TB_rolename: item.rolename,
                      TB_roleno: item.roleno
                  });
                  for(var tmp of item.functions) {
                    (that.tableData[index])[tmp] = '★';
                  }
                  index ++ ;
                }
              } else {
                that.$message.error(res.data.value);
              }

          })
          .catch((error) => {
              console.error(error);
          })
      },
      resetForm(name){
          this.dialogFormVisible = false
          this.$refs[name].resetFields();
          this.form = {
            rolename: '',
            roleno: '',
            f01: [],
            f02: [],
            f03: [],
            f04: [],
            f05: [],
            f06: [],
            f07: [],
            f08: [],
            f09: [],
            f10: [],
            f11: [],
            f12: [],
            f13: [],
            f14: [],
            f15: [],
            f16: [],
            f17: [],
            f18: [],
          }
      },
      onConfirm(){
        let that = this;
        var numberArr = [];  // function（要传给后台的权限字段），如 function:["0103","0305"]

        for(var fArr in this.form) {
          if( typeof(this.form[fArr]) === 'object' && this.form[fArr].length > 0 ){
            for( var item of this.form[fArr] ) {
              numberArr.push(item);
            }
          }
        }
        console.log(numberArr);

        var param = {
          rolename: this.form.rolename,
          roleno: this.form.roleno,
          functions: numberArr
        }
        this.$refs['form'].validate((valid) => {
            if (valid) {
                if( this.currentOpert){//新增
                    axios.post(that.URL+'/role/addition', param)
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.dialogFormVisible = false
                            that.getList();
                            that.resetForm('form');
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                      console.error(error);
                    });
                    this.dialogFormVisible = false
                }else{//修改
                    axios.post(that.URL+'/role/update', param)
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.dialogFormVisible = false
                            that.getList();
                            that.resetForm('form');
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                      console.error(error);
                    });
                    this.dialogFormVisible = false
                }
            } else {
              console.log('error submit!!');
              return false;
            }
        });
      },
      handleSelectionChange(val) {
          this.multipleSelection = val;
        //   console.log(this.multipleSelection);
      },
  },
  created(){
    this.getList();
  }
}
</script>

<style scoped>
.el-button-group{
    margin-top: 5px;
    margin-bottom: 30px;
}
.el-input,.el-select{
    width: 400px;
}
.ho-cotet .el-alert{
    background-color: #20A0FF;
}

</style>



